.performance-canvas {
  //   flex-grow: 1;
  //   width: 100%;
  //   min-height: 400px;
  //   height: 100%;
  //   max-height: 50vh;
}

.worst-best-canvas {
  width: 100%;
}

.performance-axis-y,
.performance-axis-x,
.worst-best-axis-x,
.worst-best-axis-y {
  @include ts-numerical;
  font-size: 16px;
}

.performance-circles {
  fill: $grey-dark;
  stroke: $purple-light;
  stroke-width: 2px;
  r: 5px;
}

.performance-line {
  stroke-width: 2px;
  fill: none;
  stroke: $purple-light;
}

.performance-graph-line {
  stroke: $brown-light;
  stroke-width: 1;
}

.performance-axis-odd {
  transform: translate(0, 20px);
}

.performance-label {
  @include ts-numerical;
  fill: $green-light;
  font-size: 16px;
}

.performance-label--negative {
  @include ts-numerical;
  font-size: 16px;
  fill: $red-light;
}

.worst-best-bars,
.pie-section {
  stroke: $purple-light;
  stroke-width: 2px;
  fill: rgba($purple-medium, 0.2);
}

.worst-trades-label,
.best-trades-label {
  @include ts-numerical;
  font-size: 16px;
}

.worst-trades-label {
  fill: $red-light;
}

.best-trades-label {
  fill: $green-light;
}

.worst-best-date,
.long-short-pie {
  @include ts-numerical;
  font-size: 16px;
  fill: $grey-light;
}

.worst-best-overlay-line {
  stroke: $grey-light;
  stroke-width: 2;
}

.long-short-pie {
  // @include ts-heading-3;
  // @include font-family-one;
}

.pie-chart-legend {
  transform: translate(30%, -35px);
}
